/* Page styles */
body.covid-conversation {
  background-image: url("../../assets/images/covid-conversation-background.png");
  background-repeat: no-repeat;
  background-position-x: 100%;
  background-position-y: 100px;
  background-size: 500px;
  /**
    * Page specific styling
  */ }
  body.covid-conversation h1,
  body.covid-conversation h2,
  body.covid-conversation h3,
  body.covid-conversation h4,
  body.covid-conversation h5,
  body.covid-conversation h6 {
    font-family: "League Spartan"; }
    body.covid-conversation h1.subheading,
    body.covid-conversation h2.subheading,
    body.covid-conversation h3.subheading,
    body.covid-conversation h4.subheading,
    body.covid-conversation h5.subheading,
    body.covid-conversation h6.subheading {
      font-family: "Glacial Indifference"; }
  body.covid-conversation .font-preloader {
    position: absolute;
    transform: translate(300vw); }
    body.covid-conversation .font-preloader span {
      font-family: "League Spartan"; }
    body.covid-conversation .font-preloader span:nth-child(2) {
      font-family: "Glacial Indifference"; }
  body.covid-conversation .bottom-container {
    background-color: none; }
  body.covid-conversation .drop-shadow {
    z-index: 10;
    filter: drop-shadow(3px 3px 3px #d4d5d6); }
  body.covid-conversation .circle {
    font-family: "League Spartan"; }
  body.covid-conversation .stylized-button {
    font-family: "Glacial Indifference"; }
  body.covid-conversation .page-cta {
    font-family: "League Spartan"; }
  body.covid-conversation .page.welcome .top-container {
    width: 50%;
    min-width: 550px;
    padding-top: 100px;
    background-color: rgba(255, 255, 255, 0.8); }
    body.covid-conversation .page.welcome .top-container .title-image {
      max-width: 100%; }
  body.covid-conversation .page.welcome .welcome-quote {
    padding-top: 20px;
    width: 50%;
    min-width: 550px;
    padding-left: 40px;
    background-color: rgba(255, 255, 255, 0.8); }
    body.covid-conversation .page.welcome .welcome-quote h2 {
      font-size: 64px;
      line-height: 76px; }
  body.covid-conversation .page.welcome .bottom-container {
