/* Map Styles */
body.covid-conversation .map {
  background-color: #fff;
  background-image: url("../../assets/images/covid-conversation-background.png");
  background-repeat: no-repeat;
  background-position-x: 100%;
  background-position-y: 100px;
  background-size: 500px;
  transition: background-position-x 0.8s; }
  body.covid-conversation .map h3 {
    font-size: 24px;
    padding: 8px; }
  body.covid-conversation .map .title-block {
    background: transparent; }
  body.covid-conversation .map .top-container {
    padding: 20px;
    height: auto;
    background-color: rgba(255, 255, 255, 0.8); }
  body.covid-conversation .map .knockout {
    background-color: #136494;
    color: #fff; }
  body.covid-conversation .map .map-question {
    border: 1px solid #d4d5d6;
    z-index: 10;
    filter: drop-shadow(3px 3px 3px #d4d5d6); }
    body.covid-conversation .map .map-question.expanded {
      border: 1px solid #136494; }
  body.covid-conversation .map .map-node {
    border: 1px solid #d4d5d6;
    z-index: 10;
    filter: drop-shadow(3px 3px 3px #d4d5d6); }
    body.covid-conversation .map .map-node.expanded {
      border: 1px solid #136494; }

body.covid-conversation .map-depth-selector button {
  font-size: 24px;
  width: 25%;
  color: #136494;
  background-color: #fff; }
  body.covid-conversation .map-depth-selector button.disabled {
    color: #d4d5d6; }

@media only screen and (max-width: 768px) {
  body.covid-conversation #root,
  body.covid-conversation .app,
  body.covid-conversation .map {
    height: 100%; }
  body.covid-conversation .map {
    height: 100%; }
    body.covid-conversation .map .question-list {
      width: 80%; }
    body.covid-conversation .map .map-question h3.map-question-title {
      font-size: 14px;
      margin-left: 8px; } }
