.map {
  opacity: 0;
  animation-name: fade-in;
  animation-duration: 1s;
  animation-fill-mode: forwards; }
  .map .map-container {
    transition: transform 0.8s;
    padding-bottom: 32px; }
  .map .question-list {
    position: relative;
    width: 40%; }
  .map .map-question {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: stretch;
    margin-bottom: 16px;
    background-color: #fff;
    border-radius: 0 4px 4px 0;
    cursor: pointer; }
    .map .map-question.expanded:before {
      content: '▻';
      position: absolute;
      right: 0;
      font-weight: bold;
      font-size: 48px;
      line-height: calc(48px);
      transform: scaleX(0.5); }
    .map .map-question div {
      display: inline-block;
      vertical-align: bottom; }
    .map .map-question .convo-count {
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 4px;
      margin: -1px 0 -1px -1px; }
      .map .map-question .convo-count p:first-child {
        font-size: 32px;
        line-height: 32px;
        margin: 0;
        text-align: center; }
      .map .map-question .convo-count p:nth-child(2) {
        margin: 0; }
    .map .map-question .map-question-title {
      display: flex;
      flex-direction: row;
      align-items: center;
      font-size: 32px;
      margin: 0 0 0 16px;
      padding-right: 16px; }
  .map .map-node-children,
  .map .question-children {
    position: absolute;
    left: 100%;
    top: 0;
    list-style-type: none;
    width: 100%;
    height: 100%;
    opacity: 0;
    animation-name: fade-in;
    animation-delay: 0.4s;
    animation-duration: 1s;
    animation-fill-mode: forwards; }
  .map .map-node {
    position: unset;
    width: 100%;
    padding: 8px 32px 8px 8px;
    margin-bottom: 16px;
    cursor: pointer; }
    .map .map-node.expanded {
      font-weight: bold; }
    .map .map-node.can-expand:before {
      content: '+';
      position: absolute;
      top: 8px;
      right: 8px;
      font-size: 32px;
      line-height: 32px; }
    .map .map-node.can-expand.expanded:before {
      content: '▻';
      position: absolute;
      right: 0;
      font-weight: bold;
      font-size: 32px;
      line-height: calc(32px - 3px);
      transform: scaleX(0.5); }
  .map .map-footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 32px; }
  .map .map-depth-selector button {
    width: 25%;
    border: 1px solid #d9d9d9; }
